import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { NgxSpinnerService } from 'ngx-spinner';
import { CommonService } from './common.service';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  public needToShowLoader: boolean = true;

  // Lacal Server base URL
  public APIBASEURL: string;

  constructor(
    private httpClient: HttpClient,
    public spinner: NgxSpinnerService,
    public common: CommonService,
    @Inject('BASE_URL') baseUrl: string
  ) {
    this.APIBASEURL = baseUrl;
  }



  // API Error handler Method
  errorHandler(error: Response) {
    console.log(error);

    return Observable.throw(error);
  }


  //Post response  or push data by Post method
  async postAPICallWithoutToken(APIEndpoint: string, formData: any): Promise<any> {
    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      }
      );
    })
  }

  //Post response  or push data by Post method
  async PersonInqUnifiedNumberAPICall(APIEndpoint: string, formData: any): Promise<any> {
    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object
      let userLoginInfo = this.common.getValueFromSessionStorage('userLoginInfo');
      const httpOptions = {
        headers: new HttpHeaders({
          'Token': userLoginInfo.Token ? userLoginInfo.Token : null
        })
      };
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      }
      );
    })
  }


  //Post response  or push data by Post method
  async postAPICallWithToken(APIEndpoint: string, formData: any, hideLoader: boolean = true): Promise<any> {

    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object

      let token = this.common.getValueFromSessionStorage('smartAccessTokenForSession');
      /*if (token == null) {
        (async (): Promise<void> => {
          let res = await this.httpClient.post<any>("api/EnquiryManager/SmartGenerateToken", {}).toPromise();
          token = res.access_token;
          this.common.setValueToSessionStorage('smartAccessTokenForSession', token);
        })();

      }*/

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': `Bearer Token`,
          'Token': `${token}`
        })
      };
      this.spinner.show();
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
        if (hideLoader) {
          this.spinner.hide();
        }
        //console.log("APIEndpoint ", APIEndpoint, " formData ", formData , " res ", res);
        resolve(res);
      }, err => {
        let userdata: any = {};
        userdata = this.common.getValueFromSessionStorage('userLoginInfo');
        let txt = "You are not authorized";
        let text = "You are not authorized User."; let iserror = false;
        let text1 = "You are not authorized user.";
        let msg1 = "Not Authorized.";
        if (err.error) {
          if (typeof err.error == 'string') {
            if (err.error.includes(text)) {
              iserror = true;
            }
          } else {
            if (err.error.Message == txt || err.error.Message == text || err.error.Message == text1 || err.error.Message == msg1) {
              iserror = true;
            }
          }
        }
        //console.warn("APIEndpoint ", APIEndpoint, " err ", err);
        if (err.status == 400 && userdata &&
          iserror) {
          let jsonData: any = {};
          let pass = this.common.getValueFromSessionStorage('password');
          jsonData.Username = userdata.Username;
          jsonData.Password = pass;
          this.postAPICallWithToken('api/LoginManager/Login', jsonData).then(result => {
            debugger;
            if (result.Message) {
              this.common.showToastMessage(result.Message, 2);
            } else {
              this.common.setValueToSessionStorage('userLoginInfo', result);
              userdata = this.common.getValueFromSessionStorage('userLoginInfo');
              if (formData.Token)
                formData.Token = userdata.Token;
              this.spinner.show();
              if (userdata && APIEndpoint == "api/LoginManager/Login") {
                if (hideLoader) {
                  this.spinner.hide();
                }
              } else {
                this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
                  if (hideLoader) {
                    this.spinner.hide();
                  }
                  resolve(res);
                }, err => {
                  this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
                  if (hideLoader) {
                    this.spinner.hide();
                  }
                  reject(err);
                });
              }

            }
          });

        }
        else if (err.status == 401) {
          this.httpClient.post("api/EnquiryManager/SmartGenerateToken", formData).subscribe((resGen: any) => {
            token = resGen.access_token;
            this.common.setValueToSessionStorage('smartAccessTokenForSession', token);
            const httpOptionsRed = {
              headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': `Bearer Token`,
                'Token': `${token}`
              })
            };
            this.spinner.show();
            this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptionsRed).subscribe(res => {
              if (hideLoader) {
                this.spinner.hide();
              }
              resolve(res);
            }, err => {
              this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
              if (hideLoader) {
                this.spinner.hide();
              }
              reject(err);
            });

          }, err => {
            this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
            if (hideLoader) {
              this.spinner.hide();
            }
            reject(err);
          }
          );
        }
        else {
          // If 400 / 500 / 0 error occurs while calling API then showing that error message
          if (APIEndpoint.indexOf('UserRegistration') == -1) {
            if (APIEndpoint.indexOf('Login') == -1) {
              this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
            }
          }
          if (hideLoader) {
            this.spinner.hide();
          }
          reject(err);
        }
      }
      );
    })
  }
  // For Enquiry token Start

  //Post response  or push data by Post method
  async postEnquiryAPICallWithToken(APIEndpoint: string, formData: any, hideLoader: boolean = true): Promise<any> {

    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object

      let token = this.common.getValueFromSessionStorage('enquiryAccessToken');
      //console.log("token ", token);
      if (token == null) {
        (async (): Promise<void> => {
          let res = await this.httpClient.post<any>("api/EnquiryManager/GenerateToken", {}).toPromise();
          token = res.access_token;
          this.common.setValueToSessionStorage('enquiryAccessToken', token);
        })();

      }

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': `Bearer Token`,
          'Token': `${token}`
        })
      };
      this.spinner.show();
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
        if (hideLoader) {
          this.spinner.hide();
        }
        //console.log("APIEndpoint ", APIEndpoint, " res ", res);
        resolve(res);
      }, err => {
        let userdata: any = {};
        //below code added by radhika if token will get expired we calling login api and getting token
        userdata = this.common.getValueFromSessionStorage('userLoginInfo');
        let txt = "You are not authorized";
        let text1 = "You are not authorized user.";
        let text = "You are not authorized User."; let iserror = false;
        let msg1 = "Not Authorized.";
        if (err.error) {
          if (typeof err.error == 'string') {
            if (err.error.includes(text)) {
              iserror = true;
            }
          } else {
            if (err.error.Message == txt || err.error.Message == text || err.error.Message == text1 || err.error.Message == msg1) {
              iserror = true;
            }
          }
        }
        //console.warn("APIEndpoint ", APIEndpoint, " err ", err);
        if (err.status == 400 && userdata &&
          iserror) {
          let jsonData: any = {};
          let pass = this.common.getValueFromSessionStorage('password');
          jsonData.Username = userdata.Username;
          jsonData.Password = pass;
          this.postAPICallWithToken('api/LoginManager/Login', jsonData).then(result => {
            debugger;
            if (result.Message) {
              this.common.showToastMessage(result.Message, 2);
            } else {
              this.common.setValueToSessionStorage('userLoginInfo', result);
              userdata = this.common.getValueFromSessionStorage('userLoginInfo');
              if (formData.Token)
                formData.Token = userdata.Token;
              this.spinner.show();
              if (userdata && APIEndpoint == "api/LoginManager/Login") {
                if (hideLoader) {
                  this.spinner.hide();
                }
              } else {
                this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
                  if (hideLoader) {
                    this.spinner.hide();
                  }
                  resolve(res);
                }, err => {
                  this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
                  if (hideLoader) {
                    this.spinner.hide();
                  }
                  reject(err);
                });
              }

            }
          });

        }
        else if (err.status == 401) {
          this.httpClient.post("api/EnquiryManager/GenerateToken", formData).subscribe((resGen: any) => {
            token = resGen.access_token;
            this.common.setValueToSessionStorage('enquiryAccessToken', token);
            const httpOptionsRed = {
              headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': `Bearer Token`,
                'Token': `${token}`
              })
            };
            this.spinner.show();
            this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptionsRed).subscribe(res => {
              if (hideLoader) {
                this.spinner.hide();
              }
              resolve(res);
            }, err => {
              this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
              if (hideLoader) {
                this.spinner.hide();
              }
              reject(err);
            });
          }, err => {
            this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
            if (hideLoader) {
              this.spinner.hide();
            }
            reject(err);
          }
          );
        }
        else {
          // If 400 / 500 / 0 error occurs while calling API then showing that error message
          this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
          if (hideLoader) {
            this.spinner.hide();
          }
          reject(err);
        }
      }
      );
    })
  }
  // For Enquiry token end



  //Post response  or push data by Post method
  async postAPICallforLetterwithToken(APIEndpoint: string, token: any, formData: any, hideLoader: boolean = true): Promise<any> {
    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': `Bearer Token`,
          'Token': `${token}`
        })
      };
      this.spinner.show();
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
        //console.log("APIEndpoint", APIEndpoint, " formData ", formData, " result ", res);
        if (hideLoader) {
          this.spinner.hide();
        }
        resolve(res);
      }, err => {
        // Check Type of the Error
        //console.log("APIEndpoint", APIEndpoint, " formData ", formData, " Error ", err);
        this.spinner.hide();
        if (APIEndpoint.indexOf('UserRegistration') == -1) {
          this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
        }
        reject(err);

      }
      );
    })
  }

  //Post response  or push data by Post method
  async postAPICallforRefreshToken(APIEndpoint: string, token: any, formData: any): Promise<any> {

    //Creates a new Promise.
    return await new Promise<any>((resolve, reject) => {
      //Constructs a `POST` request that interprets the body as a JSON object

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': `Bearer Token`,
          'Token': `${token}`
        })
      };
      //this.spinner.show();
      this.httpClient.post(this.APIBASEURL + APIEndpoint, formData, httpOptions).subscribe(res => {
        //console.log("APIEndpoint", APIEndpoint, " formData ", formData, " result ", res);

        resolve(res);
      }, err => {
        // Check Type of the Error
        //console.log("APIEndpoint", APIEndpoint, " formData ", formData, " Error ", err);
        //this.spinner.hide();
        this.common.showToastMessage('Something Went Worng.Please Try Again Later', 2);
        reject(err);

      }
      );
    })
  }
}
